<template>
  <aside class="mdc-drawer" style=" height:100%;top: 64px;max-height:calc(100% - 64px);width:360px;position: fixed;">

        <div class="mdc-drawer__content" v-if="run != null" >
            <label class="mdc-text-field mdc-text-field--filled" style="width: 100%">
                <span class="mdc-text-field__ripple"></span>
                <span class="mdc-floating-label" id="my-label-id" v-if="search == null || search == ''">Search run</span>
                <input class="mdc-text-field__input" type="text" v-model="search">
                <span class="mdc-line-ripple"></span>
            </label>

            <ul style="padding:0">
                <li v-for="(item, index) in filteredRuns" class="mdc-list-item"
                    v-on:click="selectRun(item, index)"
                    v-bind:class = "(item.id == selectedId)?'mdc-list-item--selected':''" tabindex="0" v-bind:key="item.id">
                <span class="mdc-list-item__ripple"></span>
                <span class="mdc-list-item__text">
                  <div class="mdc-list-item__text__child">
                  <span class="mdc-list-item__primary-text">
                      <div>{{ item.op_name }}</div>
                      <div style="font-size: 12px; margin-top: -5px;">{{item.id}}</div>
                  </span>
                  <span class="mdc-list-item__secondary-text">
                      {{item.stopped}}
                  </span>
                  </div>
                  <span v-if="item.status=='completed'" class="material-icons text-success" aria-hidden="true">done</span>
                  <span v-if="item.status=='error' && item.stopped" class="material-icons text-danger" aria-hidden="true">cancel</span>
                  <span v-if="item.status=='pending' || item.status=='running' || !item.stopped" class="material-icons text-info" aria-hidden="true">run_circle</span>
                </span>
            </li>
            </ul>

        </div>
    </aside>
</template>

<script>
export default {
    name: "SideBar",
    props:[
        'run',
        'selectedId'
    ],
    data: function () {
      return {
           search:null,
      }
    },
    methods:{
        selectRun: function(item, index){
            this.$emit("selectedRun", {"item": item, "index": index}) ;
        }
    },
    computed: {
        filteredRuns(){
         return this.run.filter(r => {
             if(this.search) {
                 return r.op_name.toLowerCase().includes(this.search.toLowerCase())
             }
             return true;
         });
        }
    },
}
</script>

<style scoped>
.mdc-list-item__text{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 90px;
}
.mdc-list-item__primary-text{

    width: 100%;
}
.mdc-list-item__text__child{
    width:100%;
    padding-right: 20px;
}

</style>