      role="alertdialog"
      aria-modal="true"
      aria-labelledby="my-dialog-title"
      aria-describedby="my-dialog-content">
      <div class="mdc-dialog__content" v-if="selectedTrain">
          <h5 style="margin-bottom: 20px;">
              Run {{selectedTrain.name}}
          </h5>
        <label class="mdc-text-field mdc-text-field--outlined" v-for="(v, k) in selectedTrain.flags" v-bind:key="v.name"
        style="width: 100%; margin-bottom: 10px;">
          <span class="mdc-notched-outline">
