<script>
import axios from "axios";
import SideBar from "@/components/SideBar.vue";
import {MDCMenu} from '@material/menu';
import {MDCDialog} from '@material/dialog';
import {MDCTextField} from '@material/textfield';
import {nextTick } from 'vue'

export default {
    name: 'App',
    data(){
        return{
            run: [],
            extraInfo: {},
            selectedId: null,
            selectedRun:null,
            menu:null,
            timer:null,
            pipelines: null,
            runIndex: null,
            dialogDeploy:null,
            dialogMessage: "Model Deployed",
            dialogTrain: null,
            selectedTrain:null,
            logs: [],
            tab: 0,
            baseUrl: ""
        }
    },
    mounted(){
        let that=this;
        this.get_pipelines();
        if(this.timer) {
            clearInterval(this.timer)
        }
        this.get_runs();
        this.timer = setInterval(function () {
            that.get_runs();
        }, 10000)

        this.menu = new MDCMenu(document.querySelector('.mdc-menu'));
        this.menu.setAnchorCorner(11)

        this.dialogDeploy = new MDCDialog(document.querySelector('.mdc-dialog.deploy'));

        this.dialogTrain = new MDCDialog(document.querySelector('.mdc-dialog.train'));

    },
    methods:{
        get_runs:function(){
            axios.get(this.baseUrl + "/compare").then((r)=>{
                this.run = []
                for(var i in r.data.run){
                    this.run.push(r.data.run[i].fmt)
                }
                if(this.run && !this.selectedRun) {
                    this.selectedId = this.run[0].id || null
                    this.selectedRun = this.run[0] || null
                    this.runIndex = 0;
                }

                Object.keys(r.data).forEach(key => {
                   if(key != 'run'){
                       this.extraInfo[key] = r.data[key]
                   }
                });
            });
        },
        get_pipelines:function(){
            axios.get(this.baseUrl + "/pipelines").then((r)=>{
                this.pipelines = r.data.pipelines
            })
        },
        selectRun:function(r){
            this.selectedId = r.item.id;
            this.selectedRun = r.item;
            this.runIndex = r.index;
            this.tab = 0;
        },
        train: function (item){
            this.selectedTrain = item
            this.dialogTrain.open();

           nextTick(() => {
             let inp = document.querySelectorAll('.mdc-dialog.train .mdc-text-field');
             inp.forEach((i)=>{
                console.log(i)
                   new MDCTextField(i);
            })
          })
        },
        submitTrain:function (){
              let that = this;
              axios.post(this.baseUrl + "/train", this.selectedTrain).then(()=>{
                  setTimeout(function(){
                      that.get_runs();
                  }, 1500);
              })
        },
        get_logs:function (id){
            axios.get(this.baseUrl + "/logs?id="+id).then((r)=>{
                this.logs = r.data;
            })
        },
    },
    watch: {
        tab(){
            this.get_logs(this.selectedRun.id)
        }
    },
    components: {
        SideBar
    }
}
</script>

